html {
  font-size: 16px;

  @include laptop() {
    font-size: 15px; }

  @include tablets() {
    font-size: 14px; }

  @include phones() {
    font-size: 13px; } }

body {
  margin: 0;
  padding: 0;
  font-family: "roboto", Arial, Helvetica, sans-serif;
  line-height: 1.4; }

*, *::before, *::after {
  box-sizing: border-box; }

body, html {
  width: 100%;
  max-width: 100%; }

ul {
  margin: 0;
  padding: 0;
  li {
    list-style: none; } }

a {
  text-decoration: none; }

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0; }
