.menu-wrapp {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  @include tr(all 0.5s ease-in-out);
  transform: translateY(-200%);
  will-change: transform;
  z-index: 40;
  overflow: hidden;

  &.active {
    transform: translateY(0%);
