.hero-section {
  background-color: #efebe0;
  background-image: url('../../img/welcom-bg.jpg');
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  height: 100vh;
  width: 100%;
  position: relative; }

.hero-container {
  width: 100%;
  height: 100%;
  position: relative; }

.hero__bg-word {
  position: absolute;
  width: 100%;
  top: 50%;
  left: 0;
  transform: translateY(-50%); }

.bg-word__img--portfolio {
  fill: rgba(#fff, 0.2);
  height: 300px; }

.hero-socials {
  position: absolute;
  left: 10px;
  top: 30px; }

.hero__avatar {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.avatar__img {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  overflow: hidden; }

.avatar__img-icon {
  width: 100%;
  height: 100%;
  object-fit: cover; }

.avatar__title-top {
  text-align: center;
  width: 100%;
  @include roboto;
  font-size: rem(35px);
  line-height: rem(45px);
  font-weight: 500;
  color: #fff; }

.avatar__title-bottom {
  width: 100%;
  text-align: center;
  @include roboto;
  font-size: rem(16px);
  line-height: rem(26px);
  font-weight: 300;
  color: #fff; }

.hero__button-down {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 70px;
  width: 30px;
  height: 20px;
  cursor: pointer;
  z-index: 10;
  @include tr(all 0.5s);

  @include hv {
    .button-down__img {
      transform: translateY(10px); } } }

.button-down__img {
  @include tr(all 0.5s);
  width: 100%;
  height: 100%;
  fill: rgba(#fff, 0.5); }

.hero__treangle-wrapp {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex; }

.trangle-left,
.trangle-reight {
  width: 50%;
  font-size: 0; }

.button-down__img {
  width: 100%;
  fill: #faf8f0; }

//slider-section

.slider-section {
  width: 100%;

  &__container {
    width: 100%; }

  &__bg-word {
    width: 100%;

    .bg-word__img--work {
      fill: #f0eee7; } }

  &__title {
    @include bernr;
    font-size: rem(60px);
    line-height: rem(70px);
    color: #373e42; } }

.section-title__wrapp {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.title__container {
  position: relative;
  // padding: 30px 0
  background-color: #faf8f0; }

.slider-wrapp {
  width: 100%;
  height: 100vh;
  background-color: #f0efe9;
  position: relative;
  display: flex;

  &__bg-left {
    background-color: #f0efe9;
    width: 50%;
    height: 100%; }

  &__bg-right {
    background-color: #efebe0;
    width: 50%;
    height: 100%; } }

.slider-container {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 50%;
  top: 0;
  transform: translateX(-50%); }

.slider {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  background-color: #f0efe9;

  &__info {
    width: 50%;
    height: 100%;
    order: 1; }

  &__views {
    background-color: #efebe0;
    width: 50%;
    height: 70%;
    order: 2;
    position: relative;
    overflow: hidden; }

  &__views-list {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    // top: 0
    // left: 0
    width: 100%;
    height: 100%; }

  &__views-item {
    padding: 0 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    @include tr(all 1s);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0;
    &.active {
      // left: 0
      opacity: 1;
      z-index: 5; } }

  &__views-img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    display: block;
    font-size: 0; }

  &__buttons {
    width: 50%;
    height: 30%;
    order: 3;
    display: flex;
    align-items: center;
    justify-content: center; }

  &__buttons-left,
  &__buttons-right {
    width: 50%;
    height: 100%;
    position: relative;
    overflow: hidden;

    &:before {
      position: absolute;
      content: "";
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: 9;
      background-color: rgba(#888372, 0.5); } }

  &__info-list {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%; }

  &__info-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 80%; } }

.info-item {
  &__title {
    @include bernr;
    font-size: rem(35px);
    line-height: rem(45px);
    color: $color-text;
    margin-bottom: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    // text-align: center
    .word {
      display: inline-block;
      margin-right: rem(10px);
      span {
        @include tr(all 0.5s);
        display: inline-block;
        opacity: 0;
        transform: translateY(5px);
        &.active {
          opacity: 1;
          transform: translateY(0); } } } }

  &__desc {
    font-weight: 500;
    font-size: rem(16px);
    line-height: rem(26px);
    @include roboto;
    color: $color-green;
    border-top: 2px solid $color-green;
    border-bottom: 2px solid $color-green;
    margin-bottom: 40px;
    width: 100%;
    padding: 20px 0;
    // text-align: center
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    .word {
      display: inline-block;
      margin-right: rem(10px);
      span {
        @include tr(all 0.5s);
        display: inline-block;
        transform: translateY(5px);
        opacity: 0;
        &.active {
          opacity: 1;
          transform: translateY(0); } } } }

  &__link {
    padding: 10px 20px;
    border-radius: 6px;
    background-color: $color-green;
    display: flex;
    justify-content: center;
    align-items: center;
    @include tr(all 0.5s);
    @include hv {
      background-color: $color-green-hv; }
    .link__img {
      width: 20px;
      height: 20px;
      fill: #fff;
      margin-right: 10px; }
    .link__text {
      @include roboto;
      font-weight: 500;
      font-size: rem(16px);
      line-height: rem(26px);
      color: #fff; } } }

.buttons-left__list,
.buttons-right__list {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

.buttons-left__item,
.buttons-right__item {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  // +tr(all 0.4s)
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  &.active {
    top: 0;
    z-index: 5; } }

.buttons-left__item {
  top: -100%; }

.buttons-right__item {
  top: 100%; }

.buttons-right__img,
.buttons-left__img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  display: block;
  font-size: 0; }

.slider__buttons-img {
  cursor: pointer;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;

  &:active {
    .slider__buttons-img-icon--up {
      // transform: translateY(-15px)
      transform: scale(1.2); }

    .slider__buttons-img-icon--down {
      // transform: translateY(15px)
      transform: scale(1.2); } } }

.slider__buttons-img-icon {
  width: 70px;
  height: 50px;
  fill: #fff;
  @include tr(all 0.2s); }

// reviews section

.feedback {
  position: relative;
  overflow: hidden;
  min-height: 100vh;

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    min-width: 900px;
    z-index: -1; }

  &__bg-img {
    position: absolute;
    bottom: -120px;
    left: 0;
    width: 100%;
    display: block;
    max-width: 100%; }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 0 150px; }

  &__arrow-up {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 20px;
    cursor: pointer; }

  &__arrow-up-img {
    width: 25px;
    height: 15px;
    fill: #fff;
    @include tr(all 0.4s);

    @include hv {
      transform: translateY(-10px); } } }

.about-title {
  padding: 15px 0 40px;
  width: 100%;
  background-color: transparent;
  text-align: center; }

.reviews {
  width: 66.6666%;
  margin-bottom: 70px;

  &__list {
    display: flex;
    justify-content: space-between; }

  &__item {
    display: flex;
    flex-direction: column;
    width: 48%;

    .fa-quote-left {
      font-size: 30px;
      color: #d5d7d8;
      margin-bottom: 30px; } }

  &__top {
    font-size: rem(16px);
    color: #455a64;
    line-height: rem(26px);
    font-weight: 400;
    width: 100%;
    margin-bottom: auto; }

  &__bottom {
    padding-top: 25px;
    display: flex;
    align-items: center;
    justify-content: flex-start; }

  &__img {
    width: 78px;
    height: 78px;
    position: relative;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 20px; }

  &__img-icon {
    position: absolute;
    width: 100%;
    top: 0;
    left: 50%;
    transform: translateX(-50%); }

  &__autor-name {
    font-weight: 700;
    font-size: rem(16px);
    line-height: rem(16px);
    color: #20b5a1;
    margin-bottom: 10px; }

  &__autor-status {
    font-weight: 400;
    font-size: rem(16px);
    line-height: rem(16px);
    color: #20b5a1; } }

.contacts-form {
  position: relative;
  overflow: hidden;
  border-radius: 6px;

  &__blur {
    position: absolute;

    height: 100%;
    width: 100%;
    filter: blur(20px);
    background-image: url("../../img/works-bottom_bg.png");
    background-position: center center;
    background-repeat: no-repeat; }

  &__form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    padding: 40px 20px 20px;
    z-index: 1; }

  &__title {
    @include bernr;
    font-weight: 400;
    font-size: rem(35px);
    line-height: rem(35px);
    color: #455a64;
    margin-bottom: 40px;
    position: relative;
    &:before,
    &:after {
      position: absolute;
      content: "";
      width: 22px;
      height: 5px;
      background-color: #beb7b2;
      top: 50%;
      transform: translateY(-50%); }

    &:before {
      left: -32px; }

    &:after {
      right: -32px; } }

  &__input,
  &__textarea {
    margin-bottom: 20px;
    padding: 15px 20px;
    font-size: rem(16px);
    line-height: rem(16px);
    color: #373e42;
    border-radius: 3px;
    border: 0;
    width: 340px;
    outline: none; }

  &__textarea {
    height: 140px;
    margin-bottom: 0;
    resize: none; }

  &__buttons {
    display: flex;
    width: 100%;
    position: relative;
    z-index: 1; }

  &__send,
  &__clear {
    font-weight: 500;
    @include roboto;
    font-size: rem(16px);
    line-height: rem(16px);
    color: #fff;
    padding: 15px 0;
    text-align: center;
    outline: none;
    background-color: $color-green;
    @include tr(all 0.4s);
    width: 50%;
    border: 0;
    cursor: pointer;

    @include hv {
      background-color: $color-green-hv; } }

  &__send {
    border-right: 2px solid #fff; } }
