body {
  margin: 0;
  padding: 0;
  font-family: "roboto", Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

*, *::before, *::after {
  box-sizing: border-box;
}

body, html {
  width: 100%;
  max-width: 100%;
}

ul {
  margin: 0;
  padding: 0;
}

ul li {
  list-style: none;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

.wrapper {
  width: 100%;
  position: relative;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.container {
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
}

.button {
  outline: transparent;
  cursor: pointer;
  white-space: nowrap;
  transition: 0.3s;
}

html,
body {
  height: 100%;
}

.wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
}

header,
footer {
  flex: 0 0 auto;
}

main {
  flex: 1 0 auto;
  position: relative;
}

.wrapper-height {
  height: auto;
}

.section {
  min-height: 500px;
}

.section-title {
  font-family: "bernierregular", serif;
  font-size: 3.75rem;
}

.sub-title {
  font-family: "bernierregular", serif;
  font-size: 2.1875rem;
  line-height: 2.1875rem;
}

.bg-word__img {
  width: 100%;
}

.visuallyhidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.clearfix:before, .clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.5s;
  transform: scale(0);
  will-change: transform;
  opacity: 0;
}

.modal-overlay.active {
  transform: scale(1);
  opacity: 1;
}

.modal-overlay.active .modal-window {
  opacity: 1;
}

.modal-window {
  padding: 20px 50px;
  background-color: #fff;
  font-family: "roboto", serif;
  font-size: 1rem;
  color: #455a64;
  text-align: center;
  border-radius: 6px;
  transition: all 0.5s;
  opacity: 0;
}

.modal-info {
  margin-bottom: 20px;
}

.modal-button {
  background-color: #00bfa5;
  border-radius: 6px;
  border: 0;
  outline: transparent;
  color: rgba(255, 255, 255, 0.8);
  font-weight: 500;
  font-size: 16px;
  transition: all 0.5s;
  padding: 15px 20px;
  cursor: pointer;
}

.modal-button:hover {
  background-color: #11958b;
}

.menu-button__wrapp {
  right: 10px;
  top: 30px;
  position: absolute;
  z-index: 50;
  width: 40px;
  height: 35px;
}

.menu-button {
  width: 40px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  cursor: pointer;
}

.menu-button__elem {
  width: 40px;
  height: 5px;
  background-color: #fff;
  border-radius: 5px;
  transition: all 0.5s;
}

.menu-button__elem:before, .menu-button__elem:after {
  position: absolute;
  left: 0;
  content: "";
  width: 40px;
  height: 5px;
  background-color: #fff;
  border-radius: 5px;
  will-change: transform;
  transition: all 0.5s;
}

.menu-button__elem:before {
  top: 0;
  transform-origin: left bottom;
}

.menu-button__elem:after {
  bottom: 0;
  transform-origin: left top;
}

.menu-button.active .menu-button__elem {
  width: 0;
}

.menu-button.active .menu-button__elem:before {
  width: 36px;
  transform: rotate(45deg) translate(2px, 0px);
}

.menu-button.active .menu-button__elem:after {
  width: 36px;
  transform: rotate(-45deg) translate(2px, 0px);
}

.menu-wrapp {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  transition: all 0.5s ease-in-out;
  transform: translateY(-200%);
  will-change: transform;
  z-index: 40;
  overflow: hidden;
}

.menu-wrapp.active {
  transform: translateY(0%);
}

.menu {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
}

.menu__overlay-left {
  transition: all 1s;
  will-change: transform;
  width: 50%;
  height: 100%;
  background-color: rgba(0, 191, 165, 0.95);
}

.menu__overlay-right {
  transition: all 1s;
  will-change: transform;
  width: 50%;
  height: 100%;
  background-color: rgba(0, 191, 165, 0.95);
}

.menu__nav-wrapp {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu__nav-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.menu__nav-item {
  width: 100%;
  margin-bottom: 50px;
  text-align: center;
}

.menu__nav-item:last-child {
  margin-bottom: 0;
}

.menu__nav-link {
  position: relative;
  font-family: "bernierregular", serif;
  font-size: 3.75rem;
  line-height: 4.375rem;
  color: #fff;
  width: 100%;
}

.menu__nav-link:before, .menu__nav-link:after {
  width: 25px;
  height: 5px;
  background-color: rgba(255, 255, 255, 0.3);
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: all 0.5s;
}

.menu__nav-link:before:hover, .menu__nav-link:after:hover {
  background-color: white;
}

.menu__nav-link:before {
  left: -25px;
}

.menu__nav-link:after {
  right: -25px;
}

@keyframes menu-animate {
  from {
    transform: translateY(-200%);
  }
  to {
    transform: translateY(0%);
  }
}

@keyframes bg-translate-left {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0%);
  }
}

@keyframes bg-translate-right {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0%);
  }
}

.socials__list {
  display: flex;
  align-items: center;
  justify-content: center;
}

.socials__item {
  margin-right: 30px;
}

.socials__item:last-child {
  margin-right: 0;
}

.socials__link {
  display: block;
}

.socials__image {
  width: 28px;
  height: 28px;
  fill: rgba(255, 255, 255, 0.5);
  transition: all 0.5s;
}

.socials__image:hover {
  fill: white;
}

.title-decor {
  position: relative;
}

.title-decor:before, .title-decor:after {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  width: 25px;
  height: 5px;
  background-color: #d1d2d2;
}

.title-decor:before {
  left: -35px;
}

.title-decor:after {
  right: -35px;
}

.footer {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #2f4347;
}

.footer-container {
  width: 100%;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-top,
.footer-bottom {
  width: 100%;
}

.footer-top {
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.footer-nav__list {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-nav__item {
  margin-right: 20px;
}

.footer-nav__item:last-child {
  margin-right: 0;
}

.footer-nav__link {
  display: block;
  font-family: "roboto", serif;
  font-weight: 500;
  font-size: 1rem;
  line-height: 1rem;
  color: #fff;
  transition: all 0.4s;
}

.footer-socials__list {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-socials__item {
  margin-right: 15px;
}

.footer-socials__item:last-child {
  margin-right: 0;
}

.footer-socials__link {
  display: block;
  width: 38px;
  height: 38px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer-socials__img {
  width: 35px;
  height: 35px;
  fill: rgba(255, 255, 255, 0.3);
  transition: all 0.4s;
}

.footer-socials__img:hover {
  fill: white;
}

.footer-info,
.footer-copyright {
  width: 50%;
  font-family: "roboto", serif;
  font-weight: 300;
  font-size: 1rem;
  line-height: 1.5rem;
  color: rgba(255, 255, 255, 0.5);
}

.footer-copyright {
  text-align: right;
}

#welcome__paralax {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: #a9c2c1;
}

.paralax__layer, .paralax__layer_cloud {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  will-change: transform;
}

.paralax__layer_img {
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 150%;
  min-height: 100%;
}

.paralax__layer_img--cloud {
  width: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  transform: translate(-85%, -15%);
  position: absolute;
}

@keyframes cloud {
  from {
    transform: translateX(0%);
  }
  too {
    transform: translateX(100%);
  }
}

.welcom-section {
  background-color: #e4e8eb;
  background-image: url("../img/welcom-bg.jpg");
  background-size: cover;
  background-position: center top;
  height: 100vh;
  position: relative;
}

.welcom-container {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all 0.5s;
}

.welcom__button {
  position: absolute;
  right: 10px;
  top: 25px;
  transition: all 0.5s;
  opacity: 1;
}

.welcom__button.active {
  transform: translateY(-100px);
  opacity: 0;
}

.welcom__button .autorisation-button {
  background-color: transparent;
  border-radius: 6px;
  border: 2px solid #fff;
  padding: 15px 30px;
  font-family: "roboto", serif;
  font-size: 1rem;
  font-weight: 500;
  color: #fff;
  transition: all 0.4s;
}

.welcom__button .autorisation-button:hover {
  color: #00bfa5;
  background-color: #fff;
}

.flipper__wrapp {
  width: 380px;
  height: 400px;
}

.flipper {
  width: 100%;
  height: 100%;
  position: relative;
  perspective: 1000px;
}

.flipper.active .flipper__front {
  -ms-transform: rotateY(180deg);
  transform: rotateY(180deg);
}

.flipper.active .flipper__back {
  -ms-transform: rotateY(360deg);
  transform: rotateY(360deg);
}

.flipper__front,
.flipper__back {
  perspective: 700px;
  background-color: rgba(255, 255, 255, 0.8);
  position: absolute;
  overflow: hidden;
  border-radius: 6px;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  backface-visibility: hidden;
  transition: all 0.5s;
  -moz-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  will-change: transform;
}

.flipper__back {
  -ms-transform: rotateY(180deg);
  transform: rotateY(180deg);
}

.front__content,
.back__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 60px 0;
}

.front__avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  overflow: hidden;
  font-size: 0;
}

.front__avatar-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.front__title {
  display: flex;
  flex-direction: column;
  width: 100%;
  text-align: center;
  color: #455a64;
  margin-bottom: 40px;
  font-family: "roboto", serif;
}

.front__title .front__title-top {
  font-weight: 500;
  font-size: 1.75rem;
  margin-bottom: 10px;
}

.front__title .front__title-bottom {
  font-weight: 40;
  font-size: 1rem;
}

.social__list {
  display: flex;
  align-items: center;
  margin-bottom: 40px;
}

.social__item {
  margin-right: 30px;
}

.social__item:last-child {
  margin-right: 0;
}

.social__img {
  width: 30px;
  height: 30px;
  fill: rgba(0, 191, 165, 0.7);
  transition: all 0.4s;
}

.social__img:hover {
  fill: #00bfa5;
}

.front__link-wrapp {
  width: 100%;
  display: flex;
}

.front__link-wrapp .front__link {
  font-family: "roboto", serif;
  color: #fff;
  font-size: 1rem;
  font-weight: 500;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #00bfa5;
  padding: 15px 0;
  border-right: 2px solid rgba(255, 255, 255, 0.8);
  transition: all 0.4s;
  flex-grow: 1;
}

.front__link-wrapp .front__link:last-child {
  border-right: 0;
}

.front__link-wrapp .front__link:hover {
  background-color: rgba(0, 150, 136, 0.85);
}

.welcom__copyright {
  font-family: "roboto", serif;
  font-weight: 300;
  font-size: 1rem;
  color: #fff;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 35px;
}

.back__content {
  padding: 35px 0 0;
}

.back__title {
  width: 100%;
  text-align: center;
  padding: 0 20px;
  font-family: "bernierregular", serif;
  font-size: 2.1875rem;
  color: #455a64;
  margin-bottom: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.back__form-wrapp {
  width: 100%;
}

.welcom__form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "roboto", serif;
  font-size: 1rem;
  font-weight: 400;
  color: #455a64;
}

.welcom__form-field {
  padding: 0 20px;
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.welcom__form-field:last-child {
  margin-bottom: 40px;
}

.form-field__img {
  align-self: stretch;
  padding: 0 15px;
  background-color: #f0efe9;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
}

.form-field__img .form-field__img-icon {
  width: 17px;
  height: 17px;
  fill: #c4cbcd;
}

.form-field__input {
  padding: 15px 20px;
  border: 0;
  color: #373e42;
  font-family: "roboto", serif;
  font-size: 1rem;
  font-weight: 400;
  width: 100%;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
  outline: transparent;
}

.form-field__label {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.form-field__check {
  display: none;
}

.form-field__check:checked + .form-field__check--fake .form-field__check-img {
  opacity: 1;
}

.form-field__check--fake {
  width: 22px;
  height: 22px;
  background-color: #00bfa5;
  margin-right: 10px;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
